import React, { useState, useEffect } from "react"

import { Container, Content } from "./styles"

export default function RSlide({
  children,
  images = [],
  height,
  interval = 8000,
}) {
  const [mounted, setMounted] = useState(false)
  const [index, setIndex] = useState(0)

  useEffect(() => setMounted(true), [])

  useEffect(() => {
    const next_index = (index + 1) % images.length
    const next = setTimeout(() => setIndex(next_index), interval)

    return () => clearTimeout(next)
  }, [index, interval, images.length])

  return (
    <Container bg="rgb(56,64,107)" height={height} className="scroll-hide">
      <Content>{children}</Content>

      {images.map((url, i) => {
        let className = ""

        if (mounted) {
          if (i === index) {
            className = "current"
          }

          if (i === (index - 1) % images.length) {
            className = "leaving"
          }
        }

        return (
          <img key={url} src={url} className={className} alt="background" />
        )
      })}
    </Container>
  )
}
